import React, { Component } from "react";
import PropTypes from 'prop-types';

export class NewsItem extends Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    imageUrl: PropTypes.string.isRequired,
    newsUrl: PropTypes.string.isRequired,
    author: PropTypes.string,
    date: PropTypes.string.isRequired,
    source: PropTypes.string.isRequired,
  };

  render() {
    let { title, description, imageUrl, newsUrl, author, date, source } = this.props;
    return (
      <div className="my-3">
        <div className="card" style={{ width: "100%", height: "100%" }}>
          <span className="position-absolute top-0 translate-middle badge rounded-pill bg-danger" style={{ left: '88%', zIndex: '1' }}>
            {source}
          </span>
          <img
            src={imageUrl || 'path/to/fallback-image.jpg'} // Fallback image
            className="card-img-top"
            alt={title} // More meaningful alt text
            style={{ height: "200px", objectFit: "cover" }}
          />
          <div className="card-body d-flex flex-column">
            <h5 className="card-title" style={{ flex: "1" }}>
              {title}
            </h5>
            <p className="card-text" style={{ flex: "1" }}>
              {description}...
            </p>
            <p className="card-text">
              <small className="text-body-secondary">
                by {!author ? "unknown" : author} on{" "}
                {new Date(date).toGMTString()}
              </small>
            </p>
            <a
              href={newsUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="btn btn-sm btn-primary"
            >
              Read More
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default NewsItem;
