import React, { Component } from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
import PropTypes from 'prop-types';

export class News extends Component {
  static propTypes = {
    country: PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string,
  };

  static defaultProps = {
    country: 'us',
    pageSize: 8,
    category: 'science',
  };

  constructor() {
    super();
    this.state = {
      articles: [],
      loading: false,
      page: 1,
      totalResults: 0,
      error: null,
    };
  }

  async componentDidMount() {
    this.fetchArticles(); // Ensure this calls the method correctly
  }

  fetchArticles = async (page = 1) => {
    this.setState({ loading: true });
    const apiKey = process.env.REACT_APP_NEWS_API_KEY;
    const apiUrl = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${apiKey}&page=${page}&pageSize=${this.props.pageSize}`;

    console.log("Fetching articles from:", apiUrl);

    try {
      let response = await fetch(apiUrl);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      let parsedData = await response.json();
      this.setState({
        articles: parsedData.articles,
        totalResults: parsedData.totalResults,
        loading: false,
        page: page,
      });
    } catch (error) {
      console.error("Error fetching articles:", error);
      this.setState({ loading: false, error: error.message });
      alert("Failed to fetch news articles. Please try again later.");
    }
  };

  // Remaining methods...

  render() {
    const { loading, articles, page, totalResults, error } = this.state;

    return (
      <div className="container my-3">
        <h1 className="text-center" style={{ margin: '40px 0px' }}>NewsMonkey - Top Headlines</h1>
        {loading && <Spinner />}
        {error && <div className="alert alert-danger">{error}</div>}
        <div className="row">
          {!loading && articles.length === 0 && !error && <div className="text-center">No articles found!</div>}
          {!loading && articles.map((article) => (
            <div className="col-md-4 col-sm-8" key={article.url}>
              <NewsItem
                title={article.title ? article.title.slice(0, 45) : ''}
                description={article.description ? article.description.slice(0, 88) : ''}
                imageUrl={article.urlToImage}
                newsUrl={article.url}
                author={article.author}
                date={article.publishedAt}
                source={article.source.name}
              />
            </div>
          ))}
        </div>
        <div className="container d-flex justify-content-between">
          <button
            disabled={page <= 1}
            type="button"
            className="btn btn-dark"
            onClick={this.handlePre}
          >
            &#x2190; Previous
          </button>
          <button
            disabled={page >= Math.ceil(totalResults / this.props.pageSize)}
            type="button"
            className="btn btn-dark"
            onClick={this.handleNext}
          >
            Next &rarr;
          </button>
        </div>
      </div>
    );
  }
}

export default News;
